import 'es6-promise/auto'
import axios from '@/request/index.js';

import {
    authorization,
    authorizationRefresh,
    authorizationVerify,
    authorizationRegister,
    activity,
    activityAMap,
    activityTicket,
	paymentPay,
	wechatAuthCode2,
	userInfo,
	payInfo
} from './restful'

const request = axios();

export default {
	/**
	     * data/pagination
	     * @param data 
	     * @param pagination 
	     * @returns {*}
	     *     pagination.index 
	     *     pagination.pageSize ()
	     *     pagination.count ()
	     *     pagination.next (null)
	     *     pagination.previous (null)
	     * @constructor
	     */
	    Data: function (data, pagination = {}) {
	        if (pagination) {
	            let offset = pagination.index
	            const limit = pagination.pageSize
	            if (offset && limit) {
	                offset = Math.round(limit * (offset - 1) + 0.4)
	                data['offset'] = offset
	                data['limit'] = limit
	            }
	        }
	        return this.UpFormData(data)
	    },
	    UpFormData: function (data) {
	        /**
	         * 
	         * 
	         * @type {boolean}
	         */
	        var objects = false
	        for (const key in data) {
	            // [object Date]
	            if (Object.prototype.toString.call(data[key]) === '[object File]') {
	                objects = true
	                console.log(Object.prototype.toString.call(data[key]))
	                continue
	            }
	        }
	        if (objects) {
	            var formData = new FormData()
	            for (const key in data) {
	                formData.append(key, data[key])
	            }
	            return formData
	        }
	        return data
	    },
	    /**
	     * JwtToken
	     * @param username 
	     * @param password 
	     * @returns {Promise<unknown>}
	     */
	    authorization: function (username, password) {
	        return Axios(authorization(), 'POST', this.Data({
	            username: username,
	            password: password
	        }))
	    },
	
	    /**
	     * JwtToken
	     * @param token
	     * @returns {Promise<unknown>}  JwtToken
	     */
	    authorizationRefresh: function (token) {
	        return Axios(authorizationRefresh(), 'POST', this.Data({
	            token: token
	        }, {}))
	    },
	
	    /**
	     * JwtToken
	     * @param token JwtToken
	     * @returns {Promise<token>} JwtToken
	     */
	    authorizationVerify: function (token) {
	        return Axios(authorizationVerify(), 'POST', this.Data({
	            token: token
	        }, {}))
	    },
	
	    /**
	     * 
	     * @param method [POST|OPTIONS] 
	     * @param data 
	     * @returns {Promise<token>}
	     */
	    authorizationRegister: function (method = 'POST', data = {}) {
	        return Axios(authorizationRegister(), method, this.Data(data))
	    },
		
		/**
		 * openid
		 * @param method 
		 * @param data 
		 * @param index 
		 * @param pagination 
		 * @returns {Promise<unknown>}
		 */
		wechatAuthCode2: function (method = 'GET', data = {}, index = null, pagination = {}) {
		    return Axios(wechatAuthCode2(index), method, this.Data(data, pagination))
		},
		
		/**
		 * 
		 * @param method 
		 * @param data 
		 * @param index 
		 * @param pagination 
		 * @returns {Promise<unknown>}
		 */
		userInfo: function (method = 'GET', data = {}, index = null, pagination = {}) {
		    return Axios(userInfo(index), method, this.Data(data, pagination))
		},
	    /**
	     * 
	     * @param method 
	     * @param data 
	     * @param index 
	     * @param pagination 
	     * @returns {Promise<unknown>}
	     */
	    activity: function (method = 'GET', data = {}, index = null, pagination = {}) {
	        return Axios(activity(index), method, this.Data(data, pagination))
	    },
		/**
		 * 
		 * @param method 
		 * @param data 
		 * @param index 
		 * @param pagination 
		 * @returns {Promise<unknown>}
		 */
		activityTicket: function (method = 'GET', data = {}, index = null, pagination = {}) {
		    return Axios(activityTicket(index), method, this.Data(data, pagination))
		},
		
		/**
		 * 
		 * @param method 
		 * @param data 
		 * @param index 
		 * @param pagination 
		 * @returns {Promise<unknown>}
		 */
		paymentPay: function (method = 'GET', data = {}, index = null, pagination = {}) {
		    return Axios(paymentPay(index), method, this.Data(data, pagination))
		},
		
		/**
		 * 
		 * @param method 
		 * @param data 
		 * @param index 
		 * @param pagination 
		 * @returns {Promise<unknown>}
		 */
		payInfo: function (method = 'GET', data = {}, index = null, pagination = {}) {
		    return Axios(payInfo(index), method, this.Data(data, pagination))
		}
}

/* 
 * @param {String} url http://xxx.xxx.xxx
 * @param {String} method [GET|POST|PUT|PATCH|DELETE|...], restfulmethod
 * @param {Object} data data */
export function Axios(url, method = 'GET', data = {}, headers = {}) {
    return new Promise((resolve, reject) => {
        let params = null
        if (method.toUpperCase() === 'GET') {
            params = data
            data = null
        }
        const validateStatus = status => {
            /*  */
			console.log('', status)
            return status >= 199 && status < 300
        }
		
        request.request({
            url: url,
            data: data,
            params: params,
            method: method,
            validateStatus: validateStatus
        }).then(response => {
            return resolve(response)
        }).catch(error => {
            return reject(error)
        })
    })
}

