import Interceptor from './core/interceptor';
import Request from './index';
// import TokenApi from '../.././../api/token';

export const globalInterceptor = {
    request: new Interceptor(),
    response: new Interceptor()
};

/**
 * 
 *  
 * `content-type`  application/json
 * `header` `content-type`    catch
 */
export const config = {
    baseURL: 'https://api.hanfuc.com', // http://127.0.0.1:8000
	// baseURL: 'http://127.0.0.1:8000',
    header: {
        'Content-Type': 'application/json'
    },
	timeout: 5000,
	withCredentials: false,
	xsrfCookieName: 'csrftoken',
	xsrfHeaderName: 'X-CSRFToken'
};

/**
 *  , 
 * : token
 *
 * `return config` 
 * `return false` catch
 * `return Promise.reject('xxxxx')` , catch
 *
 * @param {Object} config 
 */
globalInterceptor.request.use(
    (config) => {
        console.log('is global request interceptor');
        // getToken() && (config.header.token = getToken());

        return config;
    },
    (err) => {
        console.error('is global fail request interceptor: ', err);
        return false;
    }
);

/**
 *  , 
 * : 
 *
 * `return res` 
 * `return false` catch
 * `return Promise.reject('xxxxx')` , catch
 *
 * @param {Object} res 
 * @param {Object} config 
 * @return {Object|Boolean|Promise<reject>}
 */
globalInterceptor.response.use(
    async (res, config) => {
        ////////////////////////////////////////
        //  demo codehttp   //
        ////////////////////////////////////////

        console.log('is global response interceptor');

        //  `request().download()` 
        if (typeof res.tempFilePath !== 'undefined') {
            return res;
        }
		
		const data = res.data
		const statusCode = res.statusCode

        try {
            return await handleCode({ data:data, code: statusCode, config: config });
        } catch (err) {
            return Promise.reject(err);
        }
    },
    (err, config) => {
        console.error('is global response fail interceptor');
        console.error('err: ', err);
        console.error('config: ', config);

        showToast(err);

        return Promise.reject(err);
    }
);

/**
 *  `token`
 * @param {number} uid
 * @return {Promise}
 */
function getApiToken(uid) {
    return TokenApi.getMockToken(uid).then((res) => {
        return res.token;
    });
}

/**
 *  `localStorage`  `token`
 * @return {string} token
 */
function getToken() {
    return uni.getStorageSync('token');
}

/**
 *  `token`   `localStorage`
 * @param {string} token token
 * @return {void}
 */
function saveToken(token) {
    uni.setStorageSync('token', token);
}

/**
 *  http
 * @param {object} o
 * @param {object} o.data 
 * @param {object} o.config config
 * @param {string|number} o.code http
 * @return {object|Promise<reject>}
 */
function handleCode({ data, code, config }) {
    const STATUS = {
        '200'() {
            return data;
        },
		'201'() {
		    return data;
		},
        '400'() {
            // return { code, msg: '' };
            return Promise.reject({ code, msg: '' });
        },
        '401'() {
            // code401
            if (config.count === 1) {
                return Promise.reject({ code, msg: '' });
            }

            config.count++; // count
            config.url = config.instanceURL; //  config `params` 

            return getApiToken(2460392754)
                .then(saveToken)
                .then(() => Request().request(config));
        },
        '403'() {
            return Promise.reject({ code, msg: '' });
        },
        '500'() {
            return Promise.reject({ code, msg: '' });
        }
    };

    return STATUS[code] ? STATUS[code]() : Promise.reject(data, config); //  `fail`
}

// 
function showToast(data) {
    uni.showToast({
        title: JSON.stringify(data),
        icon: 'none',
        duration: 5000
    });
}
